// Chaching needed Dom Elements
const myModalBackdrop = window.document.querySelector('.my-modal-backdrop');
const myModalCloseIconContainer = window.document.querySelector(
  '.my-modal__close-icon-container'
);
const registerForm = window.document.querySelector('.register-form');
const emailInput = window.document.querySelector('.email_input');

export const closeModal = () => {
  myModalBackdrop.classList.add('hidden');
  setTimeout(() => {
    myModalBackdrop.style.display = 'none';
  }, 600);
};

export const openModal = () => {
  myModalBackdrop.style.display = 'block';
  setTimeout(() => {
    myModalBackdrop.classList.remove('hidden');
  }, 10);
};

myModalBackdrop.addEventListener('click', (e) => {
  if (e.target === myModalBackdrop) closeModal();
});

myModalCloseIconContainer.addEventListener('click', closeModal);

