// Add submit event to registerForm to  send email and close modal when click on it
registerForm.addEventListener('submit', (e) => {
  e.preventDefault();
  window.console.log(
    `Form is submited, your email is  ${
      emailInput.value || 'Not enter a value'
    }`
  );
  closeModal();
});
